// icon
.icon
  &-right-circle
    display: inline-block
    width: 113px
    height: 113px
    border-radius: 50%
    background: url('../img/common/icon-right-circle.svg') no-repeat center
    background-size: contain
    @include tablet
      width: 70px
      height: 70px
    @include sp
      width: 54px
      height: 54px
  &-down-circle
    display: inline-block
    width: 83px
    height: 83px
    border-radius: 50%
    background: url('../img/common/icon-down-circle.svg') no-repeat center
    background-size: contain
    @include tablet
      width: 70px
      height: 70px
    @include sp
      width: 54px
      height: 54px

// button
.btn
  display: inline-block
  &:not(:disabled)
    cursor: pointer
  height: 56px
  min-width: 133px
  text-align: center
  padding: 0 36px
  box-sizing: border-box
  border-radius: 28px
  font-style: normal
  font-weight: normal
  font-size: 21px
  line-height: 56px
  text-decoration: none
  @include tablet
    height: 40px
    font-size: 14px
    line-height: 40px
  @include sp
    width: 100%
  &:hover
    transition: all ease .2s
  &--white
    background: #EBEBEB
    color: #2F3B42
    border: 0.5px solid #FFFFFF
    &:not(:disabled)
      &:hover
        background: #454545
        color: #fff
  &--black
    background: #454545
    color: #fff
    border: 0.5px solid #FFFFFF
    &:not(:disabled)
      &:hover
        background: #EBEBEB
        color: #2F3B42

// form input
.form-control
  margin-bottom: 20px
  @include tablet
    margin-bottom: 15px
.input
  &-text
    width: 100%
    height: 56px
    border: 0.5px solid #FFFFFF
    box-sizing: border-box
    border-radius: 28px
    background: none
    color: #fff
    font-style: normal
    font-weight: normal
    font-size: 21px
    line-height: 56px
    padding: 0 36px
    outline: none
    @include placeholder
      color: #fff
    @include tablet
      height: 40px
      font-size: 14px
      line-height: 21px
      padding: 0 20px
    &--error
      border-color: #F4512D
      background: url('../img/common/icon-error.svg') no-repeat right 15px center
      background-size: 19px auto
    &--loading
      background: url('../img/common/icon-loading.svg') no-repeat right 17px center
      background-size: 15px auto

// message
.error-message
  font-family: Inter
  font-style: normal
  font-weight: normal
  font-size: 12px
  line-height: 18px
  color: #F4512D
  margin-top: 6px
  display: none

.img
  &-effect
    overflow: hidden
    border-radius: 20px